import { fetchBreeds, fetchCatByBreed } from './cat-api';

document.addEventListener('DOMContentLoaded', () => {
  const breedSelect = document.querySelector('.breed-select');
  const loader = document.querySelector('.loader');
  const error = document.querySelector('.error');
  const catInfo = document.querySelector('.cat-info');

  // Adugai aceast linie pentru a seta limea dorit pentru breed-select
  breedSelect.style.width = '200px'; // Putei ajusta acest numr la dimensiunea dorit

  // Ascundem loaderul i eroarea iniial
  loader.style.display = 'none';
  error.style.display = 'none';

  // ncrcm lista de rase la ncrcarea paginii
  fetchBreeds()
    .then(breeds => {
      loader.style.display = 'none'; // Ascundem loaderul la finalizarea cererii
      breeds.forEach(breed => {
        const option = document.createElement('option');
        option.value = breed.id;
        option.text = breed.name;
        breedSelect.appendChild(option);
      });
    })
    .catch(error => {
      loader.style.display = 'none'; // Ascundem loaderul n caz de eroare
      error.style.display = 'block'; // Afim eroarea
      console.error('Error fetching breeds:', error);
    });

  // Ascultm schimbrile n selector i efectum cererea pentru informaii despre pisic
  breedSelect.addEventListener('change', event => {
    const selectedBreedId = event.target.value;

    if (selectedBreedId) {
      loader.style.display = 'block'; // Afim loaderul n timpul cererii
      error.style.display = 'none'; // Ascundem eroarea, dac era afiat anterior
      catInfo.style.display = 'none'; // Ascundem informaiile despre pisic n timpul cererii

      fetchCatByBreed(selectedBreedId)
        .then(catData => {
          // Afim informaiile despre pisic
          const catImage = document.createElement('img');
          catImage.src = catData.url;
          catInfo.innerHTML = ''; // Golete coninutul existent
          catInfo.appendChild(catImage);
          const catName = document.createElement('p');
          catName.textContent = catData.breeds[0].name;
          catInfo.appendChild(catName);
          const catDescription = document.createElement('p');
          catDescription.textContent = catData.breeds[0].description;
          catInfo.appendChild(catDescription);
          const catTemperament = document.createElement('p');
          catTemperament.textContent = catData.breeds[0].temperament;
          catInfo.appendChild(catTemperament);
        })
        .catch(error => {
          loader.style.display = 'none'; // Ascundem loaderul n caz de eroare
          error.style.display = 'block'; // Afim eroarea
          console.error('Error fetching cat information:', error);
        })
        .finally(() => {
          loader.style.display = 'none'; // Ascundem loaderul dup finalizarea cererii
          catInfo.style.display = 'block'; // Afim informaiile despre pisic dup finalizarea cererii
        });
    }
  });
});
